@import 'common/mixin';
@import 'lib/variables';

@include breadcrumbs;

#sec_concept {
  text-align: center;
  position: relative;
  height: 560px;

  .concept_textarea {
    display: inline-block;
    margin-top: 110px;
    max-width: 300px;
    text-align: center;
    position: absolute;
    left: 50%;
    transform: translate(-50%,0);
    z-index: 10;

    .concept_heading {
      @include info_heading($clr_orange,#fff)
    }

    .concept_description {
      color: white;
      font-weight: 500;
      font-size: 1.1rem;
      line-height: 2;
    }
  }
  .concept_images {
    display: inline-block;
    width: 750px;
    max-width: 750px;
    height: 100%;
    position: relative;

    .illust_clip {
      position: absolute;
      width: 160px;
      top: -30px;
      right: 70px;
    }
    .illust_guide01 {
      position: absolute;
      width: 220px;
      top: 100px;
      left: 0;
    }

    .circle01s {
      position: absolute;
      top: 25px;
      left: 40px;
      width: 70px;
      height: 70px;
      border-radius: 100%;
      background-image: linear-gradient(-45deg, transparent 25%, $clr_yellow 25%, $clr_yellow 50%, transparent 50%, transparent 75%, $clr_yellow 75%, $clr_yellow);
      background-size: 8px 8px;
      transform: rotate(10deg);
    }
    .circle02s {
      position: absolute;
      top: 150px;
      right: 0;
      width: 70px;
      height: 70px;
      border-radius: 100%;
      background-image: linear-gradient(-45deg, transparent 25%, $clr_smoke-orange 25%, $clr_smoke-orange 50%, transparent 50%, transparent 75%, $clr_smoke-orange 75%, $clr_smoke-orange);
      background-size: 8px 8px;
      transform: rotate(10deg);
    }
    .circle03s {
      position: absolute;
      top: 430px;
      right: 90px;
      width: 45px;
      height: 45px;
      border-radius: 100%;
      background-image: linear-gradient(-45deg, transparent 25%, $clr_yellow 25%, $clr_yellow 50%, transparent 50%, transparent 75%, $clr_yellow 75%, $clr_yellow);
      background-size: 8px 8px;
      transform: rotate(10deg);
    }
    .circle04 {
      position: absolute;
      top: 30px;
      width: 470px;
      height: 470px;
      border-radius: 100%;
      background-image: linear-gradient(-45deg, transparent 25%, $clr_smoke-orange 25%, $clr_smoke-orange 50%, transparent 50%, transparent 75%, $clr_smoke-orange 75%, $clr_smoke-orange);
      background-size: 8px 8px;
      right: 100px;
    }
    .circle05 {
      width: 470px;
      height: 470px;
      border-radius: 100%;
      background-image: linear-gradient(-45deg, transparent 25%, $clr_yellow 25%, $clr_yellow 50%, transparent 50%, transparent 75%, $clr_yellow 75%, $clr_yellow);
      background-size: 8px 8px;
      transform: rotate(10deg);
      position: absolute;
      top: 10px;
      left: 80px;
    }
    .circle06 {
      width: 500px;
      height: 500px;
      border-radius: 100%;
      background: $clr_orange;
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%,0);
    }
  }
}

#sec_problem {
  text-align: center;
  height: 600px;
  background-image: linear-gradient(-45deg, transparent 25%, $clr_light-gray 25%, $clr_light-gray 50%, transparent 50%, transparent 75%, $clr_light-gray 75%, $clr_light-gray);
  background-size: 8px 8px;
  padding-top: 50px;

  .problem_heading {
    @include info_heading(#fff,$clr_blue)
  }

  .problem_intro {
    margin-bottom: 50px;
    text-align: center;
    font-weight: bold;
    font-size: 1.3rem;
    line-height: 1.5;
  }
  .problem_textarea {
    display: inline-block;
    width: 850px;
    max-width: 850px;
    height: 315px;
    position: relative;
  }
  .problem_textBox01,.problem_textBox02 {
    background: #fff;
    border-radius: 99999px;
    max-width: 270px;
    font-size: 1rem;
    line-height: 1.5;
    padding: 40px;

  }
  .problem_textBox01 {
    position: absolute;
    top: 0;
    left: 0;
  }
  .problem_textBox02 {
    position: absolute;
    bottom: 0;
    right: 0;
  }
  .problem_textBox_titel {
    margin-bottom: 10px;
    font-size: 1.2rem;
    font-weight: bold;
    color: #29afdd;
  }
  .illust_designer01 {
    width: 180px;
    height: auto;
    position: absolute;
    left: 50%;
    transform: translate(-50%,0);
  }
  .sample_bad {
    width: 80px;
    height: auto;
    position: absolute;
    bottom: 70px;
    left: 90px;
  }
  .sample_good {
    width: 70px;
    height: auto;
    position: absolute;
    bottom: 25px;
    left: 170px;
  }
  .sample_woman01 {
    width: 75px;
    height: auto;
    position: absolute;
    top: 70px;
    right: 175px;
  }
  .sample_woman02 {
    width: 75px;
    height: auto;
    position: absolute;
    top: 30px;
    right: 95px;
  }


}

#sec_solution {
  padding-top: 50px;
  height: 750px;
  text-align: center;
  .solution_heading {
    @include info_heading($clr_orange,$clr_light-yellow)
  }
  .solution_illustArea {
    display: inline-block;
    width: 800px;
    height: 500px;
    position: relative;
  }
  .illust_designer02 {
    width: 180px;
    height: auto;
    top: 250px;
    position: absolute;
    left: 0;
    transform: rotate(-15deg);
  }
  .illust_illustSet {
    position: absolute;
    width: 580px;
    top: 0;
    left: 48%;
    transform: translate(-50%,0);
  }
  .illust_guide04 {
    position: absolute;
    width: 180px;
    top: 270px;
    right: 0;
    transform: rotate(15deg);
  }
  .solution_description {
    display: inline-block;
    margin-bottom: 20px;
    max-width: 400px;
    text-align: center;
    font-size: 1.1rem;
    font-weight: bold;
    line-height: 1.5;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%,0);
    p {
      margin-bottom: 10px;
    }
  }
  .solution_point {
    li {
      display: inline-block;
      margin-right: 5px;
      width: 170px;
      height: 30px;
      border-radius: 99999px;
      font-weight: bold;
      padding-top: 15px;
      background: $clr_orange;
      color: $clr_white;
    }
    li:last-of-type {
      margin-right: 0;
    }
  }
}

#sec_usage {
  padding-top: 50px;
  height: 530px;
  background-image: linear-gradient(-45deg, transparent 25%, $clr_yellow 25%, $clr_yellow 50%, transparent 50%, transparent 75%, $clr_yellow 75%, $clr_yellow);
  background-size: 8px 8px;
  text-align: center;

  .usage_heading {
    @include info_heading(#fff,$clr_orange)
  }

  .usage_description {
    display: inline-block;
    margin-bottom: 30px;
    text-align: center;
    font-size: 1.1rem;
    line-height: 1.5;
    p {
      margin-bottom: 10px;
    }
  }

  .usage_scenes {
    text-align: center;

    .usage_scene {
      display: inline-block;
      margin: 0 10px;

    }
    .usage_scenes_img {
      margin-bottom: 20px;
      vertical-align: top;
      width: 200px ;
      height: 200px;
      border-radius: 50%;
      background: #fff;
      position: relative;
      img {
        display: inline-block;
        width: 100px;
        height: auto;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
      }
    }

    .usage_scenes_heading {
      font-weight: bold;
      font-size: 1.1rem;

    }
  }


}

#sec_faq {
  padding-top: 50px;
  text-align: center;
  .faq_heading {
    @include info_heading($clr_gray,#fff);
  }
  .faq_illustArea {
    display: inline-block;
    width: 800px;
    text-align: center;
    position: relative;
    .illust_designer03 {
      display: inline-block;
      width: 130px;
      height: auto;
      position: absolute;
      left: 0;
    }
    .illust_guide03 {
      display: inline-block;
      width: 130px;
      height: auto;
      position: absolute;
      right: 0;
      top: 50%;
    }
  }
  .faq_area {
    max-width: 600px;
  }
  .faq_question {
    display: inline-block;
    margin-bottom: 30px;
    padding: 20px;
    width: 450px;
    min-height: 60px;

    border-radius: 30px;
    box-sizing: border-box;
    background: #fff;
    position: relative;
    text-align: left;
    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: -30px;
      margin-top: -15px;
      border: 15px solid transparent;
      border-right: 15px solid #fff;
    }
  }
  .faq_answer {
    display: inline-block;
    margin-bottom: 30px;
    padding: 20px;
    width: 450px;
    min-height: 60px;
    border-radius: 30px;
    box-sizing: border-box;
    background: $clr_light-yellow;
    position: relative;
    text-align: left;
    &::before {
      content: "";
      position: absolute;
      top: 50%;
      right: -30px;
      margin-top: -15px;
      border: 15px solid transparent;
      border-left: 15px solid $clr_light-yellow;
    }
  }



}
@media screen and(max-width: 2000px) {

  .terms {
    height: 900px;
    padding-top: 50px;
    .title {
      text-align: center;
      margin-bottom: 50px;
      font-size: 28px;
      font-weight: bold;
      color: $clr_gray;
    }
    .baloon {
      position: relative;
      margin: 0 auto;
      margin-bottom: 30px;
      width: 500px;
      height: 100px;
      border-radius: 30px;
      .baloon-mouth {
        position: absolute;
        width: 50px;
        height: 50px;
        border-radius: 5px;
        transform: rotate(45deg);
      }
    }
    .baloon01 {
      background: $clr_white;
      .baloon-mouth01 {
        top: 25px;
        left: -15px;
        background: $clr_white;
      }
    }
    .baloon02 {
      background: $clr_light-yellow;
      .baloon-mouth02 {
        top: 25px;
        left: 460px;
        background: $clr_light-yellow;
      }
    }
    .baloon-text {
      position: absolute;
      margin: 20px;
      line-height: 30px;
      text-align: center;
      color: $clr_gray;
    }
    .illust-area {
      position: relative;
      .designer03 {
        position: absolute;
        width: 170px;
        top: -820px;
        left: -100px;
      }
      .guide03 {
        position: absolute;
        width: 200px;
        top: -350px;
        left: 650px;
      }
    }
  }
  .profile {
    height: 410px;
    background-image: linear-gradient(-45deg, transparent 25%, $clr_light-gray 25%, $clr_light-gray 50%, transparent 50%, transparent 75%, $clr_light-gray 75%, $clr_light-gray);
    background-size: 8px 8px;
    padding-top: 50px;
    .title {
      margin: 0 auto;
      margin-bottom: 30px;
      width: 150px;
      height: 40px;
      border-radius: 2px;
      background: $clr_white;
      line-height: 40px;
      text-align: center;
      font-size: 24px;
      font-weight: bold;
      color: $clr_gray;
    }
    .introduction {
      margin: 0 auto;
      width: 570px;
      .icon {
        float: left;
        margin-left: 70px;
        margin-right: 30px;
        margin-bottom: 60px;
        width: 170px;
        border-radius: 50%;
      }
      .name {
        font-size: 22px;
        color: $clr_gray;
        margin-bottom: 20px;
      }
      .position {
        font-size: 18px;
        color: $clr_gray;
        margin-bottom: 20px;
      }
      .description {
        color: $clr_gray;
        line-height: 30px;
      }
    }
    .link {
      display: block;
      margin: 0 auto;
      margin-top: 30px;
      padding: 0 20px;
      width: 270px;
      height: 40px;
      border-radius: 40px;
      line-height: 40px;
      background: $clr_orange;
      text-align: center;
      font-weight: bold;
      text-decoration: none;
      color: $clr_white;
      .symbol {
        width: 15px;
        margin-left: 8px;
      }
    }
  }
  .forms {
    height: 700px;
    padding-top: 50px;
    .title {
      margin-bottom: 30px;
      text-align: center;
      font-size: 24px;
      font-weight: bold;
      color: $clr_gray;
    }
    .description {
      margin: 0 auto;
      margin-bottom: 30px;
      width: 600px;
      line-height: 30px;
      text-align: center;
      font-weight: bold;
      color: $clr_gray;
    }
    .form {
      width: 600px;
      margin: 0 auto;
      .label {
        display: inline-block;
        width: 60px;
        color: $clr_gray;
      }
      input, textarea {
        color: $clr_gray;
        font-size: 16px;
      }
      .name {
        margin-bottom: 30px;
        padding-left: 20px;
        width: 500px;
        height: 40px;
        border-radius: 40px;
        outline: none;
        border: 0;
      }
      .content {
        padding-left: 20px;
        padding-top: 20px;
        width: 500px;
        height: 200px;
        vertical-align:top;
        border-radius: 40px;
        resize: none;
        outline: none;
        border: 0;
      }
    }
    .confirm {
      display: block;
      margin: 0 auto;
      margin-top: 30px;
      margin-bottom: 60px;
      padding: 0 20px;
      width: 150px;
      height: 40px;
      border-radius: 40px;
      line-height: 40px;
      background: $clr_orange;
      text-align: center;
      font-weight: bold;
      text-decoration: none;
      color: $clr_white;
    }
    .top {
      display: block;
      margin: 0 auto;
      margin-top: 30px;
      padding: 0 20px;
      width: 200px;
      height: 40px;
      border-radius: 40px;
      border: 1px solid $clr_orange;
      line-height: 40px;
      background: $clr_white;
      text-align: center;
      font-weight: bold;
      text-decoration: none;
      color: $clr_orange;
    }
  }
}

