.usage {
  height: 530px;
  background-image: linear-gradient(-45deg, transparent 25%, $clr_yellow 25%, $clr_yellow 50%, transparent 50%, transparent 75%, $clr_yellow 75%, $clr_yellow);
  background-size: 8px 8px;
  padding-top: 50px;
  .title {
    margin: 0 auto;
    margin-bottom: 30px;
    width: 350px;
    height: 40px;
    border-radius: 2px;
    line-height: 40px;
    background: $clr_smoke-orange;
    text-align: center;
    font-size: 24px;
    font-weight: bold;
    color: $clr_white;
  }
  .description {
    margin: 0 auto;
    margin-bottom: 20px;
    width: 600px;
    line-height: 30px;
    text-align: center;
    color: $clr_gray;
  }
  .scenes {
    display: flex;
    margin: 0 auto;
    justify-content: center;
  }
  .scene {
    margin: 20px;
  }
  .scene-img-wrapper {
    position: relative;
    margin-left: 10px;
    margin-bottom: 20px;
    width: 200px;
    height: 200px;
    background: $clr_white;
    border-radius: 50%;
    .scene-img {
      position: absolute;
      margin: 0 auto;
