@import 'lib/variables';

@mixin mediaSP {
  @media screen and (max-width:850px) {
    @content
  }
}

@mixin marginHeader {
  margin-top: 210px;
  @include mediaSP {
    margin-top: 160px;
  }
}

@mixin breadcrumbs{
  .site-navi {
    font-size: 14px;
    padding: 25px 0px 25px 0px;
    .arrow {
      margin: 0 10px 0 10px;
      color: #A1A1A1;
    }
     a {
      text-decoration: none;
      color: #A1A1A1;
       &:hover {
         color: $clr_orange;
       }
    }
  }
}

@mixin white-button(){
  width: 210px;
  border-radius: 30px;
  padding: 8px 35px 8px 35px;
  font-size: 13px;
  background: #fff;
  text-decoration: none;
  border: solid 1px #EA6D53;
  color: #EA6D53;
  outline: none;
  //transition: ease 0.4s;
  @media screen and(max-width: 500px){
    width:150px;
  }
  //&:hover{
  //  background:#EA6D53;
  //  color:#fff;
  //
  //}
}
@mixin orange-button(){
  width: 210px;
  border-radius: 30px;
