@mixin info_heading($textColor,$bgColor) {
  margin-bottom: 1rem;
  div {
    text-align: center;
  }
  span {
    display: inline-block;
    margin-bottom: 13px;
    padding: 12px 20px;
    font-size: 1.6rem;
    font-weight: bold;
    color: $textColor;
    text-align: center;
    background: $bgColor,;
  }
}

